import { TRANSLATE_INSERT, TRANSLATE_REPLACE } from '../actions';
import insert from '../commands/insert';
import replace from '../commands/replace';

export default function onAction(action, editor, next) {
  const { type, payload } = action;
  if (type === TRANSLATE_INSERT) {
    const { text } = payload;
    return insert(editor, text);
  }
  if (type === TRANSLATE_REPLACE) {
    const { text } = payload;
    return replace(editor, text);
  }
  return next();
}
